import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { EventoEndpoint } from './evento-endpoint.service';
import { AuthService } from './auth.service';
import { Evento } from '../models/evento.model';


@Injectable()
export class EventoService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private eventoEndpoint: EventoEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.eventoEndpoint.getAll<Evento[]>(page, pageSize);
  }

  getEventosByDataloggerId(dataloggerId: string) {
    return this.eventoEndpoint.getEventosByDataloggerId<Evento[]>(dataloggerId);
  }

  updateEvento(eve: Evento) {
    if (eve.id) {
      return this.eventoEndpoint.getUpdateEventoEndpoint(eve, eve.id.toString());
    }
    //else {
    //  return this.establecimientoEndpoint.getEstablecimientoByNombreEndpoint<Establecimiento>(est.nombre).pipe<Establecimiento>(
    //    mergeMap(foundEst => {
    //      est.id = foundEst.id;
    //      return this.establecimientoEndpoint.getUpdateEstablecimientoEndpoint(est, est.id.toString());
    //    }));
    //}
  }

  newEvento(eve: Evento) {
    return this.eventoEndpoint.getNewEventoEndpoint<Evento>(eve);
  }


  deleteEvento(eveOrEveId: string | Evento): Observable<Evento> {
    if (typeof eveOrEveId === 'string' || eveOrEveId instanceof String) {
      return this.eventoEndpoint.getDeleteEventoEndpoint<Evento>(<string>eveOrEveId);
    }
    else {

      if (eveOrEveId.id) {
        return this.deleteEvento(eveOrEveId.id.toString());
      }
      //else {
      //  return this.eventoEndpoint.getEventoByNombreEndpoint<Evento>(eveOrEveId.id).pipe<Evento>(
      //    mergeMap(eve => this.deleteEvento(eve.id.toString())));
      
    }
  }
}
