
import { Component, OnInit, OnDestroy, Input, EventEmitter, ViewChild, Output, TemplateRef } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { ConfiguracionEventoService } from '../../services/configuracion-evento.service';
import { ConfiguracionEvento } from 'src/app/models/configuracion-evento.model';
import { AccountService } from '../../services/account.service';
import { Permission } from '../../models/permission.model';
import { DataLoggerService } from '../../services/data-logger.service';
import { DataLogger } from 'src/app/models/data-logger.model';

@Component({
  selector: 'app-abm-configuracion-evento',
  templateUrl: './abm-configuracion-evento.component.html',
  styleUrls: ['./abm-configuracion-evento.component.css']
})
export class AbmConfiguracionEventoComponent implements OnInit, OnDestroy {
  rows: ConfiguracionEvento[] = [];
  rowsCache: ConfiguracionEvento[] = [];
  columns = [];
  editing = {};
  itemView: ConfiguracionEvento = new ConfiguracionEvento();
  itemEdit: ConfiguracionEvento = new ConfiguracionEvento();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  random: number = Math.floor((Math.random() * 6) + 1);
  _isCreate = false;
  _isClone = false;
  _rowEdit: ConfiguracionEvento = new ConfiguracionEvento();

  dataloggers: DataLogger[] = [];

  @Input() _showDataloggerEdit: number;

  //@Input()
  //_itemID: number;

  //@Input()
  //_dataLoggerID: string;

  //@Output() UpdatedItem = new EventEmitter();

    
  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;


  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModalEvento')
  viewModalEvento: ModalDirective;

  allData: ConfiguracionEvento[];

  constructor(private alertService: AlertService, private translationService: AppTranslationService,
    private localStorage: LocalStoreManager, private authService: AuthService, private configuracionEventoService: ConfiguracionEventoService, private dataLoggerService: DataLoggerService) {
  }

  private gT = (key: string) => this.translationService.getTranslation(key);

  ngOnInit() {
    this.loadingIndicator = true;

    this.dataLoggerService.getDataLoggersAvailable(3).subscribe(p => {
      this.dataloggers = p;
    });

    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });

    this.columns = [
      {
        prop: 'dataloggerID',
        name: this.gT('configuracionDeRedes.DataloggerID'),
        width: 175
      },
      {
        prop: 'encendido',
        name: this.gT('configuracionEvento.Encendido'),
        width: 130
      },
      {
        prop: 'watchdogInterno',
        name: this.gT('configuracionEvento.WatchdogInterno'),
        width: 170
      },
      {
        prop: 'defaultValues',
        name: this.gT('configuracionEvento.DefaultValues'),
        width: 180
      },
      {
        prop: 'conexionRed',
        name: this.gT('configuracionEvento.ConexionRed'),
        width: 170
      },
      {
        prop: 'desconexionRed',
        name: this.gT('configuracionEvento.DesconexionRed'),
        width: 185
      },
      {
        prop: 'espacioLibreMin',
        name: this.gT('configuracionEvento.EspacioLibreMin'),
        width: 190
      },
      {
        prop: 'bajoEspacioMemoria',
        name: this.gT('configuracionEvento.BajoEspacioMemoria'),
        width: 210
      },
      {
        prop: 'lostData',
        name: this.gT('configuracionEvento.LostData'),
        width: 150
      },
      {
        prop: 'nombre',
        name: this.gT('configuracionEvento.Nombre'),
        width: 155
      },      
      {
        name: '', width: 100, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false,
      }
    ];
  }

  ngOnDestroy() {
  }

  fetch(cb) {
    setTimeout(() => {
      this.configuracionEventoService.getAll().subscribe(e => {

        for (let dl of e) {
          for (let p of this.dataloggers)
            if (p.id == dl.dataloggerID) {
              dl.datalogger = p.id;
              break;
            }
        }

        cb(e);
      })
    }, 1000);
  }

  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }
 
  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    setTimeout(() => {
      this.itemView = row;
      this.viewModalEvento.show();
    }, 1000);
  }

  edit(row) {
    this._isCreate = false;
    this._isClone = false;
    this._showDataloggerEdit = 0;
    this._rowEdit = row;
    this.itemEdit = new ConfiguracionEvento(row);
    this.editorModal.show();
  }

  clone(row) {
    this._isClone = true;
    this._isCreate = false;
    this._showDataloggerEdit = 1;
    this._rowEdit = row;
    this.itemEdit = new ConfiguracionEvento(row);
    this.editorModal.show();
  }


  addElement() {
    setTimeout(() => {
      this._isCreate = true;
      this._isClone = false;
      this._showDataloggerEdit = 1;
      this.itemEdit = new ConfiguracionEvento();
      this.editorModal.show();
    }, 1000);
  }

  //addElement() {
  //  this.itemEdit = new ConfiguracionEvento();
  //  this.editorModal.show();
  //}

  //editElement() {
  //  this.configuracionEventoService.getConfiguracionEventoByIDEndpoint(this._itemID).subscribe(c => {
  //    this.itemEdit = c;
  //    this.editorModal.show();
  //  }, error => this.showFailedMessage(error));
  //}

  //viewElement() {
  //  this.configuracionEventoService.getConfiguracionEventoByIDEndpoint(this._itemID).subscribe(c => {
  //    this.itemEdit = this.itemView = c;
  //    this.viewModal.show();
  //  }, error => this.showFailedMessage(error));
  //}

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate)
      this.configuracionEventoService.newConfiguracionEvento(this.itemEdit).subscribe(ceve => this.createSuccessHelper(), error => this.showFailedMessage(error));
    else if (!this._isCreate && !this._isClone)
      this.configuracionEventoService.updateConfiguracionEvento(this.itemEdit).subscribe(ceve => this.updateSuccessHelper(), error => this.showFailedMessage(error));

    if (this._isClone)
      this.configuracionEventoService.newConfiguracionEvento(this.itemEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
  }

  //createSuccessHelper(id) {
  //  if (!this.itemEdit.id || this.itemEdit.id == 0) {
  //    this.itemEdit.id = id;
  //    this.UpdatedItem.emit({ id: this.itemEdit.id });
  //  }
  //  this.itemView = this.itemEdit;
  //  this.editorModal.hide();
  //  this.showSuccessMessage();
  //}

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.itemEdit);
    this.rows.splice(0, 0, this.itemEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.itemEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage( error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }


  deleteHelper(row) {
    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.configuracionEventoService.deleteConfiguracionEvento(row.id)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar'), MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }  
  }

  //getcanEditConfiguraciones() {
  //  return this.accountService.userHasPermission(Permission.manageConfiguracionesPermission);
  //}
