import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class EventoEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/evento/todos';
  private readonly _eventoUrl: string = '/api/evento/evento';
  private readonly _eventoByDataloggerUrl: string = '/api/evento/GetEventosByDataloggerId';
  //private readonly _eventoByNombreUrl: string = '/api/establecimiento/establecimiento/nombre';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getEventosByDataloggerId<T>(dataloggerId: string): Observable<T> {
    const endpointUrl = `${this._eventoByDataloggerUrl}/${dataloggerId}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getEventosByDataloggerId(dataloggerId));
      }));
  }

  /*getEstablecimientoByNombreEndpoint<T>(nombre: string): Observable<T> {
    const endpointUrl = `${this._eventoByNombreUrl}/${nombre}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getEstablecimientoByNombreEndpoint(nombre));
      }));
  }
  */

  getNewEventoEndpoint<T>(eveObject: any): Observable<T> {

    return this.http.post<T>(this._eventoUrl, JSON.stringify(eveObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewEventoEndpoint(eveObject));
      }));
  }

  getUpdateEventoEndpoint<T>(eveObject: any, eveId: string): Observable<T> {
    const endpointUrl = `${this._eventoUrl}/${eveId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(eveObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateEventoEndpoint(eveObject, eveId));
      }));
  }

  getDeleteEventoEndpoint<T>(eveId: string): Observable<T> {
    const endpointUrl = `${this._eventoUrl}/${eveId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteEventoEndpoint(eveId));
      }));
  }
}
