import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class AlarmaEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/alarma/todos';
  private readonly _alarmaUrl: string = '/api/alarma/alarma';
  private readonly _alarmaByDataloggerUrl: string = '/api/alarma/GetAlarmasByDataloggerId';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getAlarmasByDataloggerId<T>(dataloggerId: string): Observable<T> {
    const endpointUrl = `${this._alarmaByDataloggerUrl}/${dataloggerId}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAlarmasByDataloggerId(dataloggerId));
      }));
  }

  getNewAlarmaEndpoint<T>(alObject: any): Observable<T> {

    return this.http.post<T>(this._alarmaUrl, JSON.stringify(alObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewAlarmaEndpoint(alObject));
      }));
  }

  getUpdateAlarmaEndpoint<T>(alObject: any, alId: string): Observable<T> {
    const endpointUrl = `${this._alarmaUrl}/${alId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(alObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateAlarmaEndpoint(alObject, alId));
      }));
  }

  getDeleteAlarmaEndpoint<T>(alId: number): Observable<T> {
    const endpointUrl = `${this._alarmaUrl}/${alId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteAlarmaEndpoint(alId));
      }));
  }
}
