import { RouterModule, Routes, Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { DataLoggerService } from '../../services/data-logger.service';
import { DataLogger } from 'src/app/models/data-logger.model';
import { SistemaFotovoltaicoService } from '../../services/sistema-fotovoltaico.service';
import { OrdenService } from '../../services/orden.service';
import { SistemaFotovoltaico } from 'src/app/models/sistema-fotovoltaico.model';
import { Orden } from '../../models/orden.model';
import { MedicionService } from '../../services/medicion.service';
import { Medicion } from 'src/app/models/medicion.model';
import { Permission } from '../../models/permission.model';
