
import { Component, OnInit, OnDestroy, Input, TemplateRef, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { EventoService } from '../../services/evento.service';
import { Evento } from 'src/app/models/evento.model';
import { Permission } from '../../models/permission.model';
import { AccountService } from '../../services/account.service';
import { DataLoggerService } from '../../services/data-logger.service';
import { DataLogger } from 'src/app/models/data-logger.model';


@Component({
  selector: 'app-abm-evento',
  templateUrl: './abm-evento.component.html',
  styleUrls: ['./abm-evento.component.css']
})
export class AbmEventoComponent implements OnInit, OnDestroy {
  public static readonly DBKeyEventos = 'abm-evento.eventos';

  rows : Evento[] = [];
  rowsCache : Evento [] = [];
  columns = [];
  editing = {};
  eventoEdit: Evento = new Evento();
  eventoView: Evento = new Evento();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  _isCreate = false;
  _rowEdit: Evento = new Evento();

  public dataLoggers = [] = [];


  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;


  @ViewChild('fechaTemplate')
  fechaTemplate: TemplateRef<any>;

  @ViewChild('codigoTemplate')
  codigoTemplate: TemplateRef<any>;

  @ViewChild('dataloggerIDTemplate')
  dataloggerIDTemplate: TemplateRef<any>;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModalEvento')
  viewModalEvento: ModalDirective;

  allData: Evento[];

    constructor(private alertService: AlertService, private translationService: AppTranslationService, private accountService: AccountService,
    private localStorage: LocalStoreManager, private authService: AuthService, private eventoService: EventoService, private _Activatedroute: ActivatedRoute, private dataLoggerService: DataLoggerService) {
  }



  private gT = (key: string) => this.translationService.getTranslation(key);

  ngOnInit() {
    this.loadingIndicator = true;

    this.dataLoggerService.getAll().subscribe(dls => {
      this.dataLoggers = dls;
    });

    this._Activatedroute.paramMap.subscribe(params => {
      let dataLoggerID = params.get('id');
      if (dataLoggerID) {
        this.eventoService.getEventosByDataloggerId(dataLoggerID).subscribe(p => {
          this.allData = p;
        });
      }
      else {
        this.eventoService.getAll().subscribe(p => {
          this.allData = p;
        });
      }
    });


    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });
    
    this.columns = [
      {
        prop: 'fecha',
        name: this.gT('evento.Fecha'),
        cellTemplate: this.fechaTemplate, width: 80
      },
      {
        prop: 'codigo',
        name: this.gT('evento.Codigo'),
        cellTemplate: this.codigoTemplate, width: 80
      },
      {
        prop: 'dataloggerID',
        name: this.gT('evento.DataloggerID'),
        cellTemplate: this.dataloggerIDTemplate, width: 80
      },
      {
        name: '', width: 75, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false
      }
    ];
  }

  ngOnDestroy() {
  }
  
  fetch(cb) {
    setTimeout(() => {
      this.eventoService.getAll().subscribe(e => {
        this.allData = e;
        //for (let est of e) {
          
        //}

        cb(e);
      })
    }, 1000);
  }


  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }


  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.fecha, r.codigo, ));
  }


  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    this.eventoView = row;
    this.viewModalEvento.show();
  }

  edit(row) {
    this._isCreate = false;
    this._rowEdit = row;
    this.eventoEdit = new Evento(row);
    this.editorModal.show();
  }

  addElement() {
    this.formResetToggle = false;
    this._isCreate = true;

    setTimeout(() => {
      this.formResetToggle = true;

      this.eventoEdit = new Evento();
      this.editorModal.show();
    });
  }

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate) {
      this.eventoService.newEvento(this.eventoEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
      this._isCreate = false;
    }
    else
      this.eventoService.updateEvento(this.eventoEdit).subscribe(est => this.updateSuccessHelper(), error => this.showFailedMessage(error));    
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.eventoEdit);
    this.rows.splice(0, 0, this.eventoEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.eventoEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage(error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }

  //updateValue(event, cell, cellValue, row) {
  //  this.editing[row.$$index + '-' + cell] = false;
  //  this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

  //  let updatedRow = this.ClonarEvento(row);
  //  updatedRow[cell] = event.target.value;
    
  //  this.eventoService.updateEvento(updatedRow).subscribe(response => this.updateSuccessHelper(row, updatedRow), error => this.showFailedMessage(error));
  //}

  //private updateSuccessHelper(row, updatedRow) {
  //  row.fecha = updatedRow.fecha;
  //  row.codigo = updatedRow.codigo;   
  //  this.rows = [...this.rows];
  //  this.showSuccessMessage();
  //}

  private ClonarEvento(eve: Evento) {
    let newEve = new Evento();
    newEve.id = eve.id;
    newEve.fecha = eve.fecha;
    newEve.codigo = eve.codigo;

    return newEve;
  }

  deleteHelper(row) {
    this.rowsCache = this.rowsCache.filter(item => item !== row);
    this.rows = this.rows.filter(item => item !== row);

    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.eventoService.deleteEvento(row)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar') , MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }

  get canManageEventos() {
    return this.accountService.userHasPermission(Permission.manageEventosPermission);
  }
}
