
export type PermissionNames =
  'View Users' | 'Manage Users' |
  'View Roles' | 'Manage Roles' | 'Assign Roles' |
  'View Establecimientos' | 'Manage Establecimientos' |
  'View Sistemas Fotovoltaicos' | 'Manage Sistemas Fotovoltaicos' |
  'View Dataloggers' | 'Manage Dataloggers' | 'Manage Configuraciones' |
  'View Mediciones' | 'Manage Mediciones' |
  'View Alarmas' | 'Manage Alarmas' |
  'View Firmwares' | 'Manage Firmwares' |
  'View Eventos' | 'Manage Eventos' ;

export type PermissionValues =
  'users.view' | 'users.manage' |
  'roles.view' | 'roles.manage' | 'roles.assign' |
  'establecimientos.view' | 'establecimientos.manage'|
  'sistemasFotovoltaicos.view' | 'sistemasFotovoltaicos.manage' |
  'dataloggers.view' | 'dataloggers.manage' | 'configuraciones.manage' |
  'mediciones.view' | 'mediciones.manage' |
  'alarmas.view' | 'alarmas.manage' |
  'firmwares.view' | 'firmwares.manage' |
  'eventos.view' | 'eventos.manage';

export class Permission {

  public static readonly viewUsersPermission: PermissionValues = 'users.view';
  public static readonly manageUsersPermission: PermissionValues = 'users.manage';

  public static readonly viewRolesPermission: PermissionValues = 'roles.view';
  public static readonly manageRolesPermission: PermissionValues = 'roles.manage';
  public static readonly assignRolesPermission: PermissionValues = 'roles.assign';

  public static readonly viewEstablecimientosPermission: PermissionValues = 'establecimientos.view';
  public static readonly manageEstablecimientosPermission: PermissionValues = 'establecimientos.manage';

  public static readonly viewSistemasFotovoltaicosPermission: PermissionValues = 'sistemasFotovoltaicos.view';
  public static readonly manageSistemasFotovoltaicosPermission: PermissionValues = 'sistemasFotovoltaicos.manage';

  public static readonly viewDataloggersPermission: PermissionValues = 'dataloggers.view';
  public static readonly manageDataloggersPermission: PermissionValues = 'dataloggers.manage';
  public static readonly manageConfiguracionesPermission: PermissionValues = 'configuraciones.manage';

  public static readonly viewMedicionesPermission: PermissionValues = 'mediciones.view';
  public static readonly manageMedicionesPermission: PermissionValues = 'mediciones.manage';

  public static readonly viewAlarmasPermission: PermissionValues = 'alarmas.view';
  public static readonly manageAlarmasPermission: PermissionValues = 'alarmas.manage';

  public static readonly viewFirmwaresPermission: PermissionValues = 'firmwares.view';
  public static readonly manageFirmwaresPermission: PermissionValues = 'firmwares.manage';

  public static readonly viewEventosPermission: PermissionValues = 'eventos.view';
  public static readonly manageEventosPermission: PermissionValues = 'eventos.manage';

  constructor(name?: PermissionNames, value?: PermissionValues, groupName?: string, description?: string) {
    this.name = name;
    this.value = value;
    this.groupName = groupName;
    this.description = description;
  }

  public name: PermissionNames;
  public value: PermissionValues;
  public groupName: string;
  public description: string;
}
