export class Firmware {
  public id: number;
  public fechaFirmware: Date;
  public versionFirmware: string;
  public rutaArchivoFirmware: string;
  public comentarios: string;

  constructor(config?: Firmware) {
    if (config != null) {
      this.id = config.id;
      this.fechaFirmware = config.fechaFirmware;
      this.versionFirmware = config.versionFirmware;
      this.rutaArchivoFirmware = config.rutaArchivoFirmware;
      this.comentarios = config.comentarios;
    }
  }

  public Copy(config: Firmware) {
    config.id = this.id;
    config.fechaFirmware = this.fechaFirmware;
    config.versionFirmware = this.versionFirmware;
    config.rutaArchivoFirmware = this.rutaArchivoFirmware;
    config.comentarios = this.comentarios;
  }
}
