export class Establecimiento {
  public id: number;
  public nombre: string;
  public provinciaID: number;
  public provincia: string;
  public direccion: string;
  public latitudyLongitud: string;
  public nombreContacto: string;
  public telefonoContacto: string;
  public email: string;
  public comentarios: string;

  constructor(config?: Establecimiento) {
    if (config != null) {
      this.id = config.id;
      this.nombre = config.nombre;
      this.provinciaID = config.provinciaID;
      this.provincia = config.provincia;
      this.direccion = config.direccion;
      this.latitudyLongitud = config.latitudyLongitud;
      this.nombreContacto = config.nombreContacto;
      this.telefonoContacto = config.telefonoContacto;
      this.email = config.email;
      this.comentarios = config.comentarios;
    }
  }

  public Copy(config: Establecimiento) {
    config.id = this.id;
    config.nombre = this.nombre;
    config.provinciaID = this.provinciaID;
    config.provincia = this.provincia;
    config.direccion = this.direccion;
    config.latitudyLongitud = this.latitudyLongitud;
    config.nombreContacto = this.nombreContacto;
    config.telefonoContacto = this.telefonoContacto;
    config.email = this.email;
    config.comentarios = this.comentarios;
  }
}
