
import { Component, OnInit, OnDestroy, Input, TemplateRef, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { AlarmaService } from '../../services/alarma.service';
import { Alarma } from 'src/app/models/alarma.model';
import { DataLoggerService } from '../../services/data-logger.service';
import { DataLogger } from 'src/app/models/data-logger.model';
import { AccountService } from '../../services/account.service';
import { Permission } from '../../models/permission.model';

@Component({
  selector: 'app-abm-alarma',
  templateUrl: './abm-alarma.component.html',
  styleUrls: ['./abm-alarma.component.css']
})
export class AbmAlarmaComponent implements OnInit, OnDestroy {
  public static readonly DBKeyAlarmas = 'abm-alarma.alarmas';

  rows : Alarma[] = [];
  rowsCache : Alarma [] = [];
  columns = [];
  editing = {};
  alarmaView: Alarma = new Alarma();
  alarmaEdit: Alarma = new Alarma();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  _isCreate = false;
  _rowEdit: Alarma = new Alarma();

  public dataLoggers = [] = [];

  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModal')
  viewModal: ModalDirective;

  allData: Alarma[];


  constructor(private alertService: AlertService, private translationService: AppTranslationService, private dataLoggerService: DataLoggerService, private accountService: AccountService,
    private localStorage: LocalStoreManager, private authService: AuthService, private alarmaService: AlarmaService, private _Activatedroute: ActivatedRoute) {
  }

  private gT = (key: string) => this.translationService.getTranslation(key);

  onDropDownChange() {

  }

  ngOnInit() {
    this.loadingIndicator = true;

    this.dataLoggerService.getAll().subscribe(dls => {
      this.dataLoggers = dls;
    });

    this._Activatedroute.paramMap.subscribe(params => {
      let dataLoggerID = params.get('id');
      if (dataLoggerID) {
        this.alarmaService.getAlarmasByDataloggerId(dataLoggerID).subscribe(p => {
          this.allData = p;
        });
      }
      else {
        this.alarmaService.getAll().subscribe(p => {
          this.allData = p;
        });
      }
    }); 




    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });
    
    this.columns = [
      {
        prop: 'fechayHoraAlarma',
        name: this.gT('alarma.FechayHoraAlarma'),
        width: 125
      },
      {
        prop: 'dataloggerID',
        name: this.gT('alarma.DataloggerID'),
        width: 125
      },
      {
        prop: 'codigoAlarma',
        name: this.gT('alarma.CodigoAlarma'),
        width: 125
      },
      {
        prop: 'valorRegistrado',
        name: this.gT('alarma.ValorRegistrado'),
        width: 125
      },
      {
        prop: 'alarmaDisparada',
        name: this.gT('alarma.AlarmaDisparada'),
        width: 175
      },
      {
        name: '', width: 75, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false
      }
    ];
  }

  ngOnDestroy() {
  }
  
  fetch(cb) {
    setTimeout(() => {
      this.alarmaService.getAll().subscribe(e => {

        for (let el of e) {
          el.datalogger = el.dataloggerID;
        }

        cb(e);
      })
    }, 1000);
  }

  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }

  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.fechayHoraAlarma, r.codigoAlarma, r.valorRegistrado, r.alarmaDisparada, r.datalogger));
  }

  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    setTimeout(() => {
    this.alarmaView = row;
      this.viewModal.show();
    }, 1000);
  }

  edit(row) {
    setTimeout(() => {
      this._isCreate = false;
      this._rowEdit = row;
      this.alarmaEdit = new Alarma(row);      
      this.editorModal.show();
    }, 1000);
  }

  addElement() {
    setTimeout(() => {
      this._isCreate = true;
      this.alarmaEdit = new Alarma();
        this.editorModal.show();
    }, 1000);
  }

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate) {
      this.alarmaService.newAlarma(this.alarmaEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
      this._isCreate = false;
    }
    else
      this.alarmaService.updateAlarma(this.alarmaEdit).subscribe(est => this.updateSuccessHelper(), error => this.showFailedMessage(error));
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.alarmaEdit);
    this.rows.splice(0, 0, this.alarmaEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.alarmaEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage( error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }


  deleteHelper(row) {
    this.rowsCache = this.rowsCache.filter(item => item !== row);
    this.rows = this.rows.filter(item => item !== row);

    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.alarmaService.deleteAlarma(row)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar') , MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }

  get canManageAlarmas() {
    return this.accountService.userHasPermission(Permission.manageAlarmasPermission);
  }
}
