export class ConfiguracionEvento {
  public id: number;
  public dataloggerID: string;
  public datalogger: string;
  public encendido: boolean;
  public watchdogInterno: boolean;
  public defaultValues: boolean;
  public conexionRed: boolean;
  public desconexionRed: boolean;
  public espacioLibreMin: number;
  public bajoEspacioMemoria: boolean;
  public lostData: boolean;
  public nombre: string;

  constructor(config?: ConfiguracionEvento) {
    if (config != null) {
      this.id = config.id;
      this.dataloggerID = config.dataloggerID;
      this.datalogger = config.datalogger;
      this.encendido = config.encendido;
      this.watchdogInterno = config.watchdogInterno;
      this.defaultValues = config.defaultValues;
      this.conexionRed = config.conexionRed;
      this.desconexionRed = config.desconexionRed;
      this.espacioLibreMin = config.espacioLibreMin;
      this.bajoEspacioMemoria = config.bajoEspacioMemoria;
      this.lostData = config.lostData;
      this.nombre = config.nombre;     
    }
  }

  public Copy(config: ConfiguracionEvento) {
    config.id = this.id;
    config.dataloggerID = this.dataloggerID;
    config.datalogger = this.datalogger;
    config.encendido = this.encendido;
    config.watchdogInterno = this.watchdogInterno;
    config.defaultValues = this.defaultValues;
    config.conexionRed = this.conexionRed;
    config.desconexionRed = this.desconexionRed;
    config.espacioLibreMin = this.espacioLibreMin;
    config.bajoEspacioMemoria = this.bajoEspacioMemoria;
    config.lostData = this.lostData;
    config.nombre = this.nombre;
  }

}
