
import { Component, OnInit, OnDestroy, Input, TemplateRef, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';

import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { EstablecimientoService } from '../../services/establecimiento.service';
import { Establecimiento } from 'src/app/models/establecimiento.model';
import { ProvinciaService } from '../../services/provincia.service';
import { Provincia } from 'src/app/models/provincia.model';
import { AccountService } from '../../services/account.service';
import { Permission } from '../../models/permission.model';

@Component({
  selector: 'app-abm-establecimiento',
  templateUrl: './abm-establecimiento.component.html',
  styleUrls: ['./abm-establecimiento.component.css']
})
export class AbmEstablecimientoComponent implements OnInit, OnDestroy {
  public static readonly DBKeyEstablecimientos = 'abm-establecimiento.establecimientos';

  rows : Establecimiento[] = [];
  rowsCache : Establecimiento [] = [];
  columns = [];
  editing = {};
  establecimientoView: Establecimiento = new Establecimiento();
  establecimientoEdit: Establecimiento = new Establecimiento();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  _isCreate = false;
  _rowEdit: Establecimiento = new Establecimiento();

  provincias: Provincia[] = [];

  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;

  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModal')
  viewModal: ModalDirective;

  allData: Establecimiento[];

  constructor(private alertService: AlertService, private translationService: AppTranslationService, private provinciaService: ProvinciaService,
    private localStorage: LocalStoreManager, private authService: AuthService, private establecimientoService: EstablecimientoService, private accountService: AccountService) {
  }

  private gT = (key: string) => this.translationService.getTranslation(key);

  ngOnInit() {
    this.loadingIndicator = true;

    this.provinciaService.getAll().subscribe(p => {
      this.provincias = p;
    });

    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });
    
    this.columns = [
      {
        prop: 'nombre',
        name: this.gT('establecimiento.Nombre'),
        width: 125
      },
      {
        prop: 'provincia',
        name: this.gT('establecimiento.Provincia'),
        width: 125
      },
      {
        prop: 'direccion',
        name: this.gT('establecimiento.Direccion'),
        width: 125
      },
      {
        prop: 'latitudyLongitud',
        name: this.gT('establecimiento.LatitudyLongitud'),
        width: 175
      },
      {
        prop: 'nombreContacto',
        name: this.gT('establecimiento.NombreContacto'),
        width: 125
      },
      {
        prop: 'telefonoContacto',
        name: this.gT('establecimiento.TelefonoContacto'),
        width: 125
      },
      {
        prop: 'email',
        name: this.gT('establecimiento.Email'),
        width: 125
      },
      //{
      //  prop: 'comentarios',
      //  name: this.gT('establecimiento.Comentarios'),
      //  width: 200
      //},
      {
        name: '', width: 75, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false
      }
    ];
  }

  ngOnDestroy() {
  }
  
  fetch(cb) {
    setTimeout(() => {
      this.establecimientoService.getAll().subscribe(e => {

        for (let est of e) {
          for (let p of this.provincias)
            if (p.id == est.provinciaID) {
              est.provincia = p.nombre;
              break;
            }
        }

        cb(e);
      })
    }, 1000);
  }

  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }

  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.provincia));
  }

  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    setTimeout(() => {
    this.establecimientoView = row;
      this.viewModal.show();
    }, 1000);
  }

  edit(row) {
    this._isCreate = false;
    this._rowEdit = row;
    this.establecimientoEdit= new Establecimiento(row);    
    this.editorModal.show();
  }

  addElement() {
    setTimeout(() => {
    this._isCreate = true;
    this.establecimientoEdit = new Establecimiento();
    this.editorModal.show();
    }, 1000);
  }

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate) {
      this.establecimientoService.newEstablecimiento(this.establecimientoEdit).subscribe(est => this.createSuccessHelper(), error => this.showFailedMessage(error));
      this._isCreate = false;
    }
    else
      this.establecimientoService.updateEstablecimiento(this.establecimientoEdit).subscribe(est => this.updateSuccessHelper(), error => this.showFailedMessage(error));
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.establecimientoEdit);
    this.rows.splice(0, 0, this.establecimientoEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  updateSuccessHelper() {
    this.establecimientoEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage( error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }


  deleteHelper(row) {
    this.rowsCache = this.rowsCache.filter(item => item !== row);
    this.rows = this.rows.filter(item => item !== row);

    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.establecimientoService.deleteEstablecimiento(row)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar') , MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }

  get canManageEstablecimientos() {
    return this.accountService.userHasPermission(Permission.manageEstablecimientosPermission);
  }
}
