import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './components/login/login.component';
import { HomeComponent } from './components/home/home.component';
import { EstablecimientoComponent } from './components/establecimiento/establecimiento.component';
import { SistemaFotovoltaicoComponent } from './components/sistema-fotovoltaico/sistema-fotovoltaico.component';
import { MedicionComponent } from './components/medicion/medicion.component';
import { ConfiguracionDeAlarmasComponent } from './components/configuracion-de-alarmas/configuracion-de-alarmas.component';
import { ConfiguracionDeRedComponent } from './components/configuracion-de-red/configuracion-de-red.component';
import { ConfiguracionEventoComponent } from './components/configuracion-evento/configuracion-evento.component';
import { ConfiguracionMedicionesComponent } from './components/configuracion-mediciones/configuracion-mediciones.component';
import { EventoComponent } from './components/evento/evento.component';
import { DataLoggerComponent } from './components/data-logger/data-logger.component';
import { CustomersComponent } from './components/customers/customers.component';
import { SettingsComponent } from './components/settings/settings.component';
import { NotFoundComponent } from './components/not-found/not-found.component';
import { AuthService } from './services/auth.service';
import { AuthGuard } from './services/auth-guard.service';
import { AlarmaComponent } from './components/alarma/alarma.component';
import { FirmwareComponent } from './components/firmware/firmware.component';
import { AboutComponent } from './components/about/about.component';


const routes: Routes = [
  { path: '', component: HomeComponent, canActivate: [AuthGuard], data: { title: 'Home' } },
  { path: 'login', component: LoginComponent, data: { title: 'Login' } },
  { path: 'about', component: AboutComponent, data: { title: 'About' } },
  { path: 'establecimientos', component: EstablecimientoComponent, canActivate: [AuthGuard], data: { title: 'Establecimientos' } },
  { path: 'sistemas-fotovoltaicos', component: SistemaFotovoltaicoComponent, canActivate: [AuthGuard], data: { title: 'Sistemas Fotovolticos' } },
  { path: 'data-loggers', component: DataLoggerComponent, canActivate: [AuthGuard], data: { title: 'Dataloggers' } },
  { path: 'alarmas', component: AlarmaComponent, canActivate: [AuthGuard], data: { title: 'Alarmas' } },
  { path: 'firmware', component: FirmwareComponent, canActivate: [AuthGuard], data: { title: 'Firmwares' } },
  { path: 'mediciones', component: MedicionComponent, canActivate: [AuthGuard], data: { title: 'Mediciones' } },
  { path: 'mediciones/:id', component: MedicionComponent, canActivate: [AuthGuard], data: { title: 'Mediciones' } },
  { path: 'configuracion-de-alarmas', component: ConfiguracionDeAlarmasComponent, canActivate: [AuthGuard], data: { title: 'Configuracion de Alarmas' } },
  { path: 'configuracion-de-red', component: ConfiguracionDeRedComponent, canActivate: [AuthGuard], data: { title: 'Configuracion de Red' } },
  { path: 'configuracion-evento', component: ConfiguracionEventoComponent, canActivate: [AuthGuard], data: { title: 'Configuracion de Evento' } },
  { path: 'configuracion-mediciones', component: ConfiguracionMedicionesComponent, canActivate: [AuthGuard], data: { title: 'Configuracion de Mediciones' } },
  { path: 'eventos', component: EventoComponent, canActivate: [AuthGuard], data: { title: 'Eventos' } },
  { path: 'eventos/:id', component: EventoComponent, canActivate: [AuthGuard], data: { title: 'Eventos' } },
  { path: 'alarmas', component: AlarmaComponent, canActivate: [AuthGuard], data: { title: 'Alarmas' } },
  { path: 'alarmas/:id', component: AlarmaComponent, canActivate: [AuthGuard], data: { title: 'Alarmas' } },
  { path: 'settings', component: SettingsComponent, canActivate: [AuthGuard], data: { title: 'Settings' } },
  { path: 'home', redirectTo: '/', pathMatch: 'full' },
  { path: '**', component: NotFoundComponent, data: { title: 'Page Not Found' } }
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthService, AuthGuard]
})
export class AppRoutingModule { }
