
import { Component, OnInit, OnDestroy, Input, TemplateRef, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { RouterModule, Routes, Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AlertService, MessageSeverity, DialogType } from '../../services/alert.service';
import { AppTranslationService } from '../../services/app-translation.service';
import { LocalStoreManager } from '../../services/local-store-manager.service';
import { Utilities } from '../../services/utilities';
import { FirmwareService } from '../../services/firmware.service';
import { Firmware } from 'src/app/models/firmware.model';
import { AccountService } from '../../services/account.service';
import { Permission } from '../../models/permission.model';


@Component({
  selector: 'app-abm-firmware',
  templateUrl: './abm-firmware.component.html',
  styleUrls: ['./abm-firmware.component.css']
})
export class AbmFirmwareComponent implements OnInit, OnDestroy {
  public static readonly DBKeyFirmwares = 'abm-firmware.firmwares';

  rows : Firmware[] = [];
  rowsCache : Firmware [] = [];
  columns = [];
  editing = {};
  firmwareView: Firmware = new Firmware();
  firmwareEdit: Firmware = new Firmware();
  isDataLoaded = false;
  loadingIndicator = true;
  formResetToggle = true;
  _currentUserId: string;
  _hideCompletedTasks = false;
  _isCreate = false;
  _rowEdit: Firmware = new Firmware();

  

  get currentUserId() {
    if (this.authService.currentUser)
      this._currentUserId = this.authService.currentUser.id;

    return this._currentUserId;
  }

  @Input()
  verticalScrollbar = false;


  @ViewChild('fechaFirmwareTemplate')
  fechaFirmwareTemplate: TemplateRef<any>;

  @ViewChild('versionFirmwareTemplate')
  versionFirmwareTemplate: TemplateRef<any>;

  @ViewChild('rutaArchivoFirmwareTemplate')
  rutaArchivoFirmwareTemplate: TemplateRef<any>;

  @ViewChild('comentariosTemplate')
  comentariosTemplate: TemplateRef<any>;
    
  @ViewChild('actionsTemplate')
  actionsTemplate: TemplateRef<any>;

  @ViewChild('editorModal')
  editorModal: ModalDirective;

  @ViewChild('viewModalFirmware')
  viewModalFirmware: ModalDirective;

  allData: Firmware[];

  constructor(private alertService: AlertService, private translationService: AppTranslationService,
    private localStorage: LocalStoreManager, private authService: AuthService, private firmwareService: FirmwareService, private accountService: AccountService) {
  //  this.establecimientoService.getAll().subscribe(d => {
  ////    this.allData = d;
  ////    console.log(this.allData);
  //    })
  }



  private gT = (key: string) => this.translationService.getTranslation(key);

  ngOnInit() {
    this.loadingIndicator = true;

   

    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });
    
    this.columns = [
      {
        prop: 'fechaFirmware',
        name: this.gT('firmware.FechaFirmware'),
        cellTemplate: this.fechaFirmwareTemplate, width: 125
      },
      {
        prop: 'versionFirmware',
        name: this.gT('firmware.VersionFirmware'),
        cellTemplate: this.versionFirmwareTemplate, width: 125
      },
      {
        prop: 'rutaArchivoFirmware',
        name: this.gT('firmware.RutaArchivoFirmware'),
        cellTemplate: this.rutaArchivoFirmwareTemplate, width: 125
      },
      {
        prop: 'comentarios',
        name: this.gT('firmware.Comentarios'),
        cellTemplate: this.comentariosTemplate, width: 125
      },
      {
        name: '', width: 75, cellTemplate: this.actionsTemplate, resizeable: false, canAutoResize: false, sortable: false,
        draggable: false
      }
    ];
  }

  getData() {
    this.fetch((data) => {
      this.refreshDataIndexes(data);
      this.rows = data;
      this.rowsCache = [...data];
      this.isDataLoaded = true;

      setTimeout(() => { this.loadingIndicator = false; }, 1500);
    });
  }

  ngOnDestroy() {
  }
  
  fetch(cb) {
    setTimeout(() => {
      this.firmwareService.getAll().subscribe(e => {

        for (let est of e) {
          
        }

        cb(e);
      })
    }, 1000);
  }


  refreshDataIndexes(data) {
    let index = 0;

    for (const i of data) {
      i.$$index = index++;
    }
  }


  onSearchChanged(value: string) {
    this.rows = this.rowsCache.filter(r => Utilities.searchArray(value, false, r.fechaFirmware, r.versionFirmware, ));
  }


  showErrorAlert(caption: string, message: string) {
    this.alertService.showMessage(caption, message, MessageSeverity.error);
  }

  view(row) {
    setTimeout(() => {
      this.firmwareView = row;
      this.viewModalFirmware.show();
    }, 1000);
  }

  edit(row) {
    setTimeout(() => {
      this._isCreate = false;
      this._rowEdit = row;
      this.firmwareEdit = new Firmware(row);
      this.editorModal.show();
    }, 1000);
  }

  addElement() {
    this.formResetToggle = false;
    this._isCreate = true;
    setTimeout(() => {
      this.formResetToggle = true;

      this.firmwareEdit = new Firmware();
      this.editorModal.show();
    });
  }

  save() {
    this.alertService.startLoadingMessage(this.gT('abm.Guardando'));

    if (this._isCreate) {
      this.firmwareService.newFirmware(this.firmwareEdit).subscribe(fw => this.createSuccessHelper(), error => this.showFailedMessage(error));
      this._isCreate = false;
    }
    else
      this.firmwareService.updateFirmware(this.firmwareEdit).subscribe(fw => this.updateSuccessHelper(), error => this.showFailedMessage(error));
  }

  updateSuccessHelper() {
    this.firmwareEdit.Copy(this._rowEdit);
    this.editorModal.hide();
    this.showSuccessMessage();
  }

  createSuccessHelper() {
    this.rowsCache.splice(0, 0, this.firmwareEdit);
    this.rows.splice(0, 0, this.firmwareEdit);
    this.refreshDataIndexes(this.rowsCache);
    this.rows = [...this.rows];
    this.editorModal.hide();
    this.showSuccessMessage();
  }
    

  private showSuccessMessage() {
    this.alertService.stopLoadingMessage();
    this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlGuardar'), MessageSeverity.success);
  }

  private showFailedMessage( error: any) {
    this.alertService.stopLoadingMessage();
    this.alertService.showStickyMessage(this.gT('abm.ErrorAlGuardar'), error.error, MessageSeverity.error, error);
  }

  delete(row) {
    this.alertService.showDialog(this.gT('abm.EstaSeguro'), DialogType.confirm, () => this.deleteHelper(row));
  }


  deleteHelper(row) {
    this.rowsCache = this.rowsCache.filter(item => item !== row);
    this.rows = this.rows.filter(item => item !== row);

    this.alertService.startLoadingMessage(this.gT('abm.Eliminando'));
    this.loadingIndicator = true;

    this.firmwareService.deleteFirmware(row)
      .subscribe(results => {
        this.alertService.stopLoadingMessage();
        this.loadingIndicator = false;

        this.rowsCache = this.rowsCache.filter(item => item !== row);
        this.rows = this.rows.filter(item => item !== row);

        this.alertService.stopLoadingMessage();
        this.alertService.showMessage(this.gT('abm.Exito'), this.gT('abm.ExitoAlEliminar') , MessageSeverity.success);
      },
        error => {
          this.alertService.stopLoadingMessage();
          this.loadingIndicator = false;

          this.alertService.showStickyMessage(this.gT('abm.ErrorAlEliminar'),
            `Error: "${Utilities.getHttpResponseMessage(error)}"`,
            MessageSeverity.error, error);
        });
  }

  get canManageFirmwares() {
    return this.accountService.userHasPermission(Permission.manageFirmwaresPermission);
  }
}
