import { Injectable, Injector } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { EndpointFactory } from './endpoint-factory.service';
import { ConfigurationService } from './configuration.service';


@Injectable()
export class EstablecimientoEndpoint extends EndpointFactory {

  private readonly _todosUrl: string = '/api/establecimiento/todos';
  private readonly _establecimientoUrl: string = '/api/establecimiento/establecimiento';
  private readonly _establecimientoByNombreUrl: string = '/api/establecimiento/establecimiento/nombre';

  constructor(http: HttpClient, configurations: ConfigurationService, injector: Injector) {
    super(http, configurations, injector);
  }

  getAll<T>(page?: number, pageSize?: number): Observable<T> {
    const url = page && pageSize ? `${this._todosUrl}/${page}/${pageSize}` : this._todosUrl;

    return this.http.get<T>(url, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getAll(page, pageSize));
      }));
  }

  getEstablecimientoByNombreEndpoint<T>(nombre: string): Observable<T> {
    const endpointUrl = `${this._establecimientoByNombreUrl}/${nombre}`;

    return this.http.get<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getEstablecimientoByNombreEndpoint(nombre));
      }));
  }

  getNewEstablecimientoEndpoint<T>(estObject: any): Observable<T> {

    return this.http.post<T>(this._establecimientoUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getNewEstablecimientoEndpoint(estObject));
      }));
  }

  getUpdateEstablecimientoEndpoint<T>(estObject: any, estId: string): Observable<T> {
    const endpointUrl = `${this._establecimientoUrl}/${estId}`;

    return this.http.put<T>(endpointUrl, JSON.stringify(estObject), this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getUpdateEstablecimientoEndpoint(estObject, estId));
      }));
  }

  getDeleteEstablecimientoEndpoint<T>(estId: string): Observable<T> {
    const endpointUrl = `${this._establecimientoUrl}/${estId}`;

    return this.http.delete<T>(endpointUrl, this.getRequestHeaders()).pipe<T>(
      catchError(error => {
        return this.handleError(error, () => this.getDeleteEstablecimientoEndpoint(estId));
      }));
  }
}
