import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { SistemaFotovoltaicoEndpoint } from './sistema-fotovoltaico-endpoint.service';
import { AuthService } from './auth.service';
import { SistemaFotovoltaico } from '../models/sistema-fotovoltaico.model';


@Injectable()
export class SistemaFotovoltaicoService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private sistemaFotovoltaicoEndpoint: SistemaFotovoltaicoEndpoint) {

  }

  getAll(page?: number, pageSize?: number) {
    return this.sistemaFotovoltaicoEndpoint.getAll<SistemaFotovoltaico[]>(page, pageSize);
  }

  updateSistemaFotovoltaico(est: SistemaFotovoltaico) {
    if (est.id) {
      return this.sistemaFotovoltaicoEndpoint.getUpdateSistemaFotovoltaicoEndpoint(est, est.id.toString());
    }
    else {
      return this.sistemaFotovoltaicoEndpoint.getSistemaFotovoltaicoByEstablecimientoEndpoint<SistemaFotovoltaico>(est.establecimientoID).pipe<SistemaFotovoltaico>(
        mergeMap(foundEst => {
          est.id = foundEst.id;
          return this.sistemaFotovoltaicoEndpoint.getUpdateSistemaFotovoltaicoEndpoint(est, est.id.toString());
        }));
    }
  }

  newSistemaFotovoltaico(sf: SistemaFotovoltaico) {
    return this.sistemaFotovoltaicoEndpoint.getNewSistemaFotovoltaicoEndpoint<SistemaFotovoltaico>(sf);
  }


  deleteSistemaFotovoltaico(sfId: number): Observable<SistemaFotovoltaico> {
    return this.sistemaFotovoltaicoEndpoint.getDeleteSistemaFotovoltaicoEndpoint<SistemaFotovoltaico>(sfId);
  }
}
