export class ConfiguracionDeRed {
  public dataloggerID: string;
  public id: number;
  public dhcpEthernet: boolean;
  public direccionIpEthernet: string;
  public mascaraSubredEthernet: string;
  public PuertaEnlaceEthernet: string;
  public dnsPrimarioEthernet: string;
  public dnsSecundarioEthernet: string;
  public ssidWifi: string;
  public contrasenaWifi: string;
  public seguridadWifi: string;
  public dchpWifi: boolean;
  public direccionIpWifi: string;
  public mascaraSubredWifi: string;
  public puertaEnlaceWifi: string;
  public dnsPrimarioWifi: string;
  public dnsSecundarioWifi: string;
  public urlServidorCentral: string;
  public puertoServidorCentral: number;
  public timeOutConexionServidor: number;
  public timeOutIntercambioDatosServidor: number;
  public reintentosReconexionPorCiclo: number;
  public tiempoEsperaEntreReconexiones: number;
  public tiempoEsperaRecomienzoDeCiclo: number;
  public comentarios: string;
  public datalogger: string;

  constructor(config?: ConfiguracionDeRed) {
    if (config != null) {
      this.id = config.id;
      this.dataloggerID = config.dataloggerID;
      this.datalogger = config.datalogger;
      this.dhcpEthernet = config.dhcpEthernet;
      this.direccionIpEthernet = config.direccionIpEthernet;
      this.mascaraSubredEthernet = config.mascaraSubredEthernet;
      this.PuertaEnlaceEthernet = config.PuertaEnlaceEthernet;
      this.dnsPrimarioEthernet = config.dnsPrimarioEthernet;
      this.dnsSecundarioEthernet = config.dnsSecundarioEthernet;
      this.ssidWifi = config.ssidWifi;
      this.contrasenaWifi = config.contrasenaWifi;
      this.seguridadWifi = config.seguridadWifi;
      this.dchpWifi = config.dchpWifi;
      this.direccionIpWifi = config.direccionIpWifi;
      this.mascaraSubredWifi = config.mascaraSubredWifi;
      this.puertaEnlaceWifi = config.puertaEnlaceWifi;
      this.dnsPrimarioWifi = config.dnsPrimarioWifi;
      this.dnsSecundarioWifi = config.dnsSecundarioWifi;
      this.urlServidorCentral = config.urlServidorCentral;
      this.puertoServidorCentral = config.puertoServidorCentral;
      this.timeOutConexionServidor = config.timeOutConexionServidor;
      this.timeOutIntercambioDatosServidor = config.timeOutIntercambioDatosServidor;
      this.reintentosReconexionPorCiclo = config.reintentosReconexionPorCiclo;
      this.tiempoEsperaEntreReconexiones = config.tiempoEsperaEntreReconexiones;
      this.tiempoEsperaRecomienzoDeCiclo = config.tiempoEsperaRecomienzoDeCiclo;
      this.comentarios = config.comentarios;      
    }
  }

  public Copy(config: ConfiguracionDeRed) {
    config.id = this.id;
    config.dataloggerID = this.dataloggerID;
    config.datalogger = this.datalogger;
    config.dhcpEthernet = this.dhcpEthernet;
    config.direccionIpEthernet = this.direccionIpEthernet;
    config.mascaraSubredEthernet = this.mascaraSubredEthernet;
    config.PuertaEnlaceEthernet = this.PuertaEnlaceEthernet;
    config.dnsPrimarioEthernet = this.dnsPrimarioEthernet;
    config.dnsSecundarioEthernet = this.dnsSecundarioEthernet;
    config.ssidWifi = this.ssidWifi;
    config.contrasenaWifi = this.contrasenaWifi;
    config.seguridadWifi = this.seguridadWifi;
    config.dchpWifi = this.dchpWifi;
    config.direccionIpWifi = this.direccionIpWifi;
    config.mascaraSubredWifi = this.mascaraSubredWifi;
    config.puertaEnlaceWifi = this.puertaEnlaceWifi;
    config.dnsPrimarioWifi = this.dnsPrimarioWifi;
    config.dnsSecundarioWifi = this.dnsSecundarioWifi;
    config.urlServidorCentral = this.urlServidorCentral;
    config.puertoServidorCentral = this.puertoServidorCentral;
    config.timeOutConexionServidor = this.timeOutConexionServidor;
    config.timeOutIntercambioDatosServidor = this.timeOutIntercambioDatosServidor;
    config.reintentosReconexionPorCiclo = this.reintentosReconexionPorCiclo;
    config.tiempoEsperaEntreReconexiones = this.tiempoEsperaEntreReconexiones;
    config.tiempoEsperaRecomienzoDeCiclo = this.tiempoEsperaRecomienzoDeCiclo;
    config.comentarios = this.comentarios;      
  }

}
