import { Injectable } from '@angular/core';
import { Router, NavigationExtras } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { Observable, Subject, forkJoin } from 'rxjs';
import { mergeMap, tap } from 'rxjs/operators';

import { OrdenEndpoint } from './orden-endpoint.service';
import { AuthService } from './auth.service';
import { Orden } from '../models/orden.model';


@Injectable()
export class OrdenService {
  
  constructor(private router: Router, private http: HttpClient, private authService: AuthService,
    private ordenEndpoint: OrdenEndpoint) {

  }

  newOrden(dataloggerID: string, tipoID: number) {
    return this.ordenEndpoint.getNewOrdenEndpoint(dataloggerID, tipoID);
  }

  getByDataloggerID(dataloggerID: string) {
    return this.ordenEndpoint.getByDataloggerID<Orden[]>(dataloggerID);
  }
}
